import React from 'react';
import './dialog.css'
import SendMessageContainer from './sendMessage/sendMessage_container'

const Dialog = (props) => {
  return (
    <div className="dialog-window">
      <img className="dialog-window__bg" src="https://abrakadabra.fun/uploads/posts/2022-01/1642093334_1-abrakadabra-fun-p-oboi-dlya-vaiber-1.jpg" />
      <div className="dialog-window__head">
        <button><i>&times;</i></button>
        <h3>{props.name}</h3>
      </div>
      <div className="dialog-window__scroll">
          {props.messages}
      </div>
      <SendMessageContainer />
    </div>
  );
};

export default Dialog;