import React from 'react';
import './message.css'

const Message = (props) => {
  const isMe = props.addressee === 'Me';
  return (
    <div className={ isMe ? 'message_wrap my-message' : 'message_wrap' }>
      <div className='message'>
        <span className="message__addressee">
          { isMe ? '' : props.addressee }
        </span>
        <p className="message__text">
          {props.text}
        </p>
      </div>
    </div>
  );
};

export default Message;