import React from 'react';
import Message from './message/message';
import Dialog from './dialog';
import { connect } from 'react-redux';

const mapStateToProps = (state) => {
  if(state.dialogsPage.openedDialog) {
    return {
      name: state.dialogsPage.openedDialog.name,
      messages: state.dialogsPage.openedDialog.messages.map((message, index) => {
        return <Message addressee={message.addressee} text={message.text} key={index}/>
      })
    }
  }
},
DialogContainer = connect(mapStateToProps, null)(Dialog)

export default DialogContainer;